<template>
  <div class="my clearfix">
    <div class="bg pr" :style="{backgroundImage: 'url(' + userInfo.userBg + ')'}">
      <!-- <img :src="userInfo.userBg" alt /> -->
      <div class="mask pa">
        <h2>
          <van-uploader class="replace-bg" :after-read="upload">
            <span>更换背景</span>
          </van-uploader>
        </h2>
      </div>
      <div class="avatar">
        <img :src="userInfo.userImg" alt />
      </div>
      <div class="txt pa">
        <h3 v-if="isLogin">{{userInfo.nickName}}</h3>
        <h3 v-else @click="goState()">登录 / 注册</h3>
        <p>{{userInfo.desc == '' ? '这个人很懒，什么都没有留下' : userInfo.desc}}</p>
      </div>
    </div>

    <div class="list clearfix">
      <van-cell-group>
        <van-cell
          v-for="(item,index) in cellData"
          :key="index"
          :title="item.title"
          is-link
          size="large"
          @click="goPage(item.name)"
        />
      </van-cell-group>
    </div>

    <div class="fixd"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";

import { utils } from "../utils/utils";

export default {
  name: "My",

  created() {
    this.$store.commit("myModule/changeData", { key: "userInfo", value: {} });
    this.getUserInfo();
  },

  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.vanTabbar = true;
  },

  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    ...mapState("myModule", ["cellData", "userInfo"])
  },

  methods: {
    goPage(name) {
      if (!this.isLogin) {
        this.$toast("请先登录");
        return;
      }
      this.$router.push({ name });
    },

    goState() {
      this.$router.push({ name: "Login" });
    },

    //获取用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("_t");

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "加载中..."
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();

          this.$store.commit("changeData", {
            key: "isLogin",
            value: result.data.code != 700
          });

          if (result.data.code == 700) {
            return;
          }

          this.$store.commit("myModule/changeData", {
            key: "userInfo",
            value: result.data.result[0]
          });
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    //上传头像
    upload(file) {
      if (!this.isLogin) {
        this.$toast("请先登录");
        return;
      }

      //控制上传图片大小不能超过 300KB
      let imgSize = 300 * 1024;
      if (file.file.size > imgSize) {
        this.$toast("上传图片大小不能超过300KB");
        return;
      }

      //将图片上传到服务器

      //获取图片类型
      let imgType = file.file.type.split("/")[1];

      //处理图片的base64
      let serverBase64Img = file.content.replace(
        /data:image\/[A-Za-z]+;base64,/,
        ""
      );

      let tokenString = localStorage.getItem("_t");

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "加载中..."
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img
        },
        transformRequest: utils.transformRequest
      })
        .then(result => {
          this.$toast.clear();

          this.userInfo.userBg = result.data.userBg;
        })
        .catch(err => {
          this.$toast.clear();
        });
    }
  }

};
</script>

<style lang="less" scoped>
.van-divider {
  margin: 0;
}
.my {
  background-color: #f9f9f9;

  .bg {
    height: 300px;
    background-size: cover;
    img {
      position: fixed;
      height: 45%;
    }
    .mask {
      background-color: rgba(0, 0, 0, 0.4);
      height: 100%;
      width: 100%;
      z-index: 0;
      h2 {
        color: #ccc;
        font-size: 12px;
        font-weight: 400;
        margin-top: 20px;
        float: right;
      }
    }
    .avatar {
      z-index: 1000;
      img {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        margin-top: calc((300px - 180px) / 2);
        margin-left: 30px;
      }
    }
    .txt {
      h3 {
        color: #fff;
        margin-top: 170px;
        margin-left: 30px;
        font-size: 16px;
        margin-bottom: 15px;
      }
      p {
        color: #ccc;
        font-size: 12px;
        margin-left: 30px;
      }
    }
  }
  .list {
    margin: -50px 10px;
    box-shadow: 1px 1px 5px #ccc;
    z-index: 10010;
  }
  .fixd {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #f9f9f9;
    z-index: -1;
  }
}

/deep/ .van-uploader {
  margin-right: 14px;
}

/deep/ .van-uploader__upload {
  // width: 20px;
  height: 20px;
  margin: 0px;
  background-color: transparent;
  border-radius: 0;
}

/deep/ .van-uploader__upload-icon {
  font-size: 12px;
}
</style>